// src/contexts/ApiContext.tsx
"use client";

import { createContext, useContext, useState, useCallback } from "react";
import api, {
  chatWithAI,
  login as apiLogin,
  register as apiRegister,
} from "../services/api";
import {
  ChatMessageDTO,
  ChatApiResponse,
  LoginDTO,
  RegisterDTO,
} from "../types/index";

interface ApiContextValue {
  token: string | null;
  setToken: (t: string | null) => void;
  chat: (payload: ChatMessageDTO) => Promise<ChatApiResponse>;
  login: (payload: LoginDTO) => Promise<any>;
  register: (payload: RegisterDTO) => Promise<any>;
}

const ApiContext = createContext<ApiContextValue | undefined>(undefined);

export function ApiProvider({ children }: { children: React.ReactNode }) {
  const [token, setTokenState] = useState<string | null>(() => {
    if (typeof window === "undefined") return null;
    return localStorage.getItem("token");
  });

  const setToken = useCallback((t: string | null) => {
    setTokenState(t);
    if (typeof window === "undefined") return;
    if (t) localStorage.setItem("token", t);
    else localStorage.removeItem("token");
  }, []);

  const login = useCallback(
    async (payload: LoginDTO) => {
      const data = await apiLogin(payload);
      console.log("LOGIN RESPONSE FRONT:", data);

      // AJUSTE AQUI se seu backend usa outro formato
      // Exemplos de caminhos comuns:
      // { token: "..." }
      // { accessToken: "..." }
      // { success: true, data: { token: "..." } }
      const token =
        data.token ||
        data.accessToken ||
        data?.data?.token ||
        data?.data?.accessToken;

      if (token) {
        setToken(token);
      } else {
        console.warn(
          "Nenhum token encontrado na resposta de login. Ajuste o caminho do token no ApiContext."
        );
      }

      return data;
    },
    [setToken]
  );

  const register = useCallback(async (payload: RegisterDTO) => {
    const data = await apiRegister(payload);
    return data;
  }, []);

  const chat = useCallback(async (payload: ChatMessageDTO) => {
    const resp = await chatWithAI(payload);
    return resp;
  }, []);

  // Sempre que o token muda, atualiza o header default do axios
  if (token) {
    api.defaults.headers.common.Authorization = `Bearer ${token}`;
  } else {
    delete api.defaults.headers.common.Authorization;
  }

  const value: ApiContextValue = {
    token,
    setToken,
    chat,
    login,
    register,
  };

  return <ApiContext.Provider value={value}>{children}</ApiContext.Provider>;
}

export const useApi = (): ApiContextValue => {
  const ctx = useContext(ApiContext);
  if (!ctx) throw new Error("useApi deve ser usado dentro de ApiProvider");
  return ctx;
};
